.hero__link {
  text-transform: uppercase;
  color: #EF7F1A;
  position: relative;
  font-weight: 700;
  transition-timing-function: ease-in-out;
  transition-duration: 300ms;
  transition-property: all;
}

.hero__link:before {
  content: '';
  position: absolute;
  width: 100%;
  height: 1px;
  bottom: 0px;
  left: 0px;
  background: currentColor;
  transition: inherit;
  transform-origin: left;
}

@media (any-hover: hover) {
  .hero__link:hover:before {
    transform: scaleX(0);
  }
}

.hero__link:focus:before {
  transform: scaleX(0);
}

.hero__content {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 2;
  top: 0px;
  left: 0px;
}

